<template>
    <div id="hobby">
        <h2>사진</h2>

        <div className="pics_slide">
            <button className="pics pics_left" @click="goToPrevPic">&nbsp;&nbsp;&nbsp;&#10094;&nbsp;&nbsp;</button>
            <img v-bind:src="picsArray[activePics]" alt="" id="slide_img"/>
            <button className="pics pics_right" @click="goToNextPic">&nbsp;&nbsp;&#10095;&nbsp;&nbsp;&nbsp;</button>
        </div>

        <p>날씨가 좋은 날이면 사진기를 들고 밖으로 나갑니다. 학기 중에는 바빠서 꾸준히 찍기가 힘들 때도 있지만 일주일에 한 번은 꼭 나가려고 노력하고 있습니다.</p>
        <p>현재는 여름 방학 동안 찍은 사진으로 작은 책을 내기 위해서 친구와 열심히 회의를 하고 있습니다.</p>
        <br />

        <h2>독서</h2>
        <div className="books_slide">
            <button className="books books_left" @click="goToPrevBook">&nbsp;&nbsp;&nbsp;&#10094;&nbsp;&nbsp;</button>
            <img v-bind:src="booksArray[activeBooks]" alt="" id="slide_img"/>
            <button className="books books_right" @click="goToNextBook">&nbsp;&nbsp;&#10095;&nbsp;&nbsp;&nbsp;</button>
        </div>
        <p>보통 지하철에서 책을 읽습니다. 집에서 읽으려 해도 방해요소가 너무 많아서 그런지 도저히 집중이 안됩니다.</p>
        <p>최근 읽은 책들 중 재미있게 봐서 추천하고 싶은 책들입니다.</p>
        <br />
    </div>
</template>

<script>
    export default {
        data() {
            return {
                picsArray: [
                require('@/assets/image/hobby/pics/01.jpg'),
                require('@/assets/image/hobby/pics/02.jpg'),
                require('@/assets/image/hobby/pics/03.jpg'),
                require('@/assets/image/hobby/pics/04.jpg'),
                require('@/assets/image/hobby/pics/05.jpg'),
                require('@/assets/image/hobby/pics/06.jpg'),
                require('@/assets/image/hobby/pics/07.jpg'),
                require('@/assets/image/hobby/pics/08.jpg'),
                require('@/assets/image/hobby/pics/09.jpg'),
                require('@/assets/image/hobby/pics/10.jpg')
                ],
                booksArray: [
                require('@/assets/image/hobby/books/01.jpg'),
                require('@/assets/image/hobby/books/02.jpg'),
                require('@/assets/image/hobby/books/03.jpg'),
                require('@/assets/image/hobby/books/04.jpg'),
                require('@/assets/image/hobby/books/05.jpg'),
                require('@/assets/image/hobby/books/06.jpg'),
                require('@/assets/image/hobby/books/07.jpg')
                ],
                activePics: 0,
                activeBooks: 0
            }
        },
        methods: {
            goToNextPic: function() {
                if (this.activePics === this.picsArray.length - 1) {
                    this.activePics = 0;
                    return;
                }
                this.activePics = this.activePics + 1;
            },
            goToPrevPic: function() {
                if (this.activePics === 0) {
                    this.activePics = this.picsArray.length - 1;
                    return;
                }
                this.activePics = this.activePics - 1;
            },
            goToNextBook: function() {
                if (this.activeBooks === this.booksArray.length - 1) {
                    this.activeBooks = 0;
                    return;
                }
                this.activeBooks = this.activeBooks + 1;
            },
            goToPrevBook: function() {
                if (this.activeBooks === 0) {
                    this.activeBooks = this.booksArray.length - 1;
                    return;
                }
                this.activeBooks = this.activeBooks - 1;
            }
        }
    }
</script>

<style>
    .pics_slide, .books_slide {
        position: relative;
        width: 100%;
    }
    .pics, .books {
        position: absolute;
        bottom: 45%;
        font-size: 1.5em;
        opacity: .5;
    }
    .pics_left, .books_left {
        right: 20%;
    }
    #slide_img {
        width: 20%;
        height: 30%;
    }
</style>