<template>
    <div>
        <h1>연락</h1>
        <p>해당 폼 작성을 통해 메세지를 보내거나</p>
        <p>charlesbest@naver.com 다음 이메일로 직접 연락을 해주세요</p>
        <form @submit.prevent="handleSubmit()">
            <p>
            <input type="text" name="name" size="45" v-model="formData.name" placeholder="이름"/>
            </p>
            <p>
            <input type="text" name="email" size="45" v-model="formData.email" placeholder="이메일"/>
            </p>
            <p>
            <textarea name="msg" cols="50" rows="10" v-model="formData.msg" placeholder="메세지"></textarea>
            </p>
            <p>
            <button type="submit">보내기</button>
            </p>
        </form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                formData: {
                    name: '',
                    email: '',
                    msg: ''
                }
            }
        },
        methods: {
            handleSubmit: function() {
                alert(`이름: ${this.formData.name}\n 이메일: ${this.formData.email}\n 메세지: ${this.formData.msg}`);
            }
        }
    }
</script>

