<template>
    <div>
        <ol id="button_list">
            <li v-for=" (component, index) in componentsArray" v-bind:key="index">
                <button @click="swapComponent(component)">{{ tabs[index] }}</button>
            </li>
        </ol>
        <br /> <br />
        <component :is="currentComponent"></component>
    </div>
</template>

<script>
    import Project1Cmt from '@/components/Project/Project1Cmt';
    import Project2Cmt from '@/components/Project/Project2Cmt';
    import Project3Cmt from '@/components/Project/Project3Cmt';

    export default {
        data() {
            return {
                currentComponent: 'Project1Cmt',
                componentsArray: ['Project1Cmt', 'Project2Cmt', 'Project3Cmt'],
                tabs: ['프로젝트1', '프로젝트2', '프로젝트3']
            }
        },
        components: {
            Project1Cmt,
            Project2Cmt,
            Project3Cmt
        },
        methods: {
            swapComponent: function(component) {
                this.currentComponent = component;
            }
        }
    };
</script>

