<template>
    <div>
        <ol id="button_list">
            <li v-for=" (component, index) in componentsArray" v-bind:key="index">
                <button @click="swapComponent(component)">{{ tabs[index] }}</button>
            </li>
        </ol>
        <br /> <br />
        <component :is="currentComponent"></component>
    </div>
</template>

<script>
    import Career1Cmt from "@/components/Career/Career1Cmt";
    import Career2Cmt from "@/components/Career/Career2Cmt";
    import Career3Cmt from "@/components/Career/Career3Cmt";

    export default {
        data() {
            return {
                currentComponent: 'Career1Cmt',
                componentsArray: ['Career1Cmt', 'Career2Cmt', 'Career3Cmt'],
                tabs: ['학력', '경력', '자격증/어학/교내수상']
            }
        },
        components: {
            Career1Cmt,
            Career2Cmt,
            Career3Cmt
        },
        methods: {
            swapComponent: function(component) {
                this.currentComponent = component;
            }
        }
    };
</script>

