import { createWebHistory, createRouter } from "vue-router";

import HomeCmt from "@/components/HomeCmt";
import IntroductionCmt from "@/components/IntroductionCmt";
import ProjectCmt from "@/components/ProjectCmt";
import CareerCmt from "@/components/CareerCmt";
import ContactCmt from "@/components/ContactCmt";
import MyPageCmt from "@/components/MyPageCmt";

const routes = [
    {
        path: '/',
        redirect: { name: 'Home' }
    },
    {
        path: "/",
        name: 'Home',
        component: HomeCmt
    },
    {
        path: "/introduction",
        component: IntroductionCmt
    },
    {
        path: "/project",
        component: ProjectCmt
    },
    {
        path: "/career",
        component: CareerCmt
    },
    {
        path: "/contact",
        component: ContactCmt
    },
    {
        path: "/MyPage/김민석/개발자",
        component: MyPageCmt
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes
});

export default router;