<template>
    <div>
        <ol id="button_list">
            <li v-for=" (component, index) in componentsArray" v-bind:key="index">
                <button @click="swapComponent(component)">{{ tabs[index] }}</button>
            </li>
        </ol>
        <br /> <br />
        <component :is="currentComponent"></component>
    </div>
</template>

<script>
    import AboutCmt from "@/components/Intro/AboutCmt";
    import HobbyCmt from "@/components/Intro/HobbyCmt";

    export default {
        data() {
            return {
                currentComponent: 'AboutCmt',
                componentsArray: ['AboutCmt', 'HobbyCmt'],
                tabs: ['기본 정보', '취미']
            }
        },
        components: {
            AboutCmt,
            HobbyCmt
        },
        methods: {
            swapComponent: function(component) {
                this.currentComponent = component;
            }
        }
    };
</script>

